<template>
  <div>
    <div style="position: fixed; z-index:200; top:300px">
      <object data="https://o799k6nkb.qnssl.com/hamster.swf" height="200" width="256" style="outline:none;"
              type="application/x-shockwave-flash;">
      </object>
    </div>
    <div class="container">
      <meta name="referrer" content="never">
      <Col span="5" align="center" v-for="(book,index) in books">
        <div v-bind:class="{'element-card' : true,'open' : rotaIndex == index}" v-on:click="clickCard(index)">
          <div class="front-facing">
            <p slot="title">{{book.name}}</p>
            <img class="bookimg" :src="book.bookImageUrl" alt="">
            <p>售价：{{book.price}}</p>
          </div>
          <div class="back-facing">
            <p style="overflow-y: scroll;">{{book.intro}}</p>
            <Button @click="buy(book)" >加入购物车</Button>
          </div>
        </div>
      </Col>
    </div>
    <div class="page-number">
      <Page :total="bookCount" :current="pageNum" :page-size='12' simple @on-change="getBook"></Page>
    </div>
  </div>
</template>
<script>

  export default {

    name: 'List',
    computed: {
      books: {
        get: function () {
          return this.$store.state.bookInfo;
        },
        set: function (newValue) {
          this.$store.state.bookInfo = newValue;
        }
      },
      bookCount: {
        get: function () {
          return this.$store.state.bookCount;
        }
      }
    },
    data() {
      return {
        isActive: true,
        rotaIndex: -1,
        pageNum: 1,
      }
    },
    created: function () {
      this.$store.dispatch('getBookCount');
      this.$store.dispatch('getBook', this.pageNum);
      if (this.bookCount < 1) {
        this.bookCount = 1;
      }
    },
    methods: {
      clickCard: function (index) {
        this.rotaIndex = this.rotaIndex == index ? -1 : index;
      },
      buy(book) {
        this.$store.dispatch('buyBook', book);
      },
      getBook(index) {
        this.pageNum = index;
        this.$store.dispatch('getBookCount');
        this.$store.dispatch('getBook', this.pageNum);
      },
    }
  }
</script>
<style>
  .bookimg {
    width: 200px;
    height: 250px;
  }

  .element-card {
    position: relative;
    width: 300px;
    height: 300px;
    padding: auto;
    -webkit-transform-origin: center top;
    transform-origin: center top;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    transition: all 0.6s linear;
    -webkit-transition: all 0.6s linear;
    box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.4);
    cursor: pointer;
    margin: 5px;
    z-index: 50;
  }

  .element-card:hover {
    -webkit-transform-origin: center top;
    transform-origin: center top;
    transform: rotateY(30deg) translateX(0px) translateY(0px);
  }

  .element-card.open {
    -webkit-transform: rotateY(-180deg) scale(1.5);
    transform: rotateY(-180deg) scale(1.5);
    z-index: 999;
  }

  .element-card .front-facing {
    -webkit-transform: rotateY(0deg) translateZ(2px);
    transform: rotateY(0deg) translateZ(2px);
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #ecf0f1;
    border: 2px white solid;
    border-radius: 5px;
    z-index: 1;
  }

  .element-card .back-facing {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #ecf0f1;
    border: 2px white solid;
    border-radius: 5px;
    padding: 20px;
    text-align: center;

  }

  .element-card .back-facing p {
    color: #666;
    text-align: justify;
    text-indent: 2em;
    line-height: 22px;
    font-size: 13px;
    margin: 0;
  }

  .container {
    margin-top: 60px;
    left: 0;
    right: 0;
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }

  .page-number {
    width: 190px;
    height: 40px;
    left: 0;
    right: 0;
    margin: auto;
  }
</style>




