import {
  BUY_BOOK, GET_BOOK_COUNT, GET_BOOK,
  GET_USER_BOOKS, BOOK_ADD, BOOK_UPDATE, BOOK_DELETE,
  GET_CART, CANCEL_BUY, PAY,
  LOGIN, REGISTER, LOGOUT,GET_USER_INFO
} from './types'
import axi from 'axios';

var axios = axi.create({
  baseURL: "http://localhost:8081"
});
var setCookie = function (cname, cvalue, exdays) {
  var d = new Date();
  d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
  var expires = "expires=" + d.toUTCString();
  document.cookie = cname + "=" + cvalue + "; " + expires;
};
const getCookie = function (c_name) {
  if (document.cookie.length > 0) {
    var c_start = document.cookie.indexOf(c_name + "=");
    if (c_start != -1) {
      c_start = c_start + c_name.length + 1;
      var c_end = document.cookie.indexOf(";", c_start);
      if (c_end == -1) c_end = document.cookie.length;
      return unescape(document.cookie.substring(c_start, c_end));
    }
  }
  return "";
};
const mutations = {
  [BUY_BOOK](state, book) { //把一本书加入购物车 1(成功），2（失败），3（添加了自己的书）
    axios.post('/list/buyBook', {userId: state.userInfo.id, bookId: book.id})
      .then(response => {
        if (response.data == 1) {
          state.cart.push(book);
          state.bookInfo.splice(state.bookInfo.indexOf(book), 1);
        } else if (response.data == 2) {
          alert('加入购物车失败，请重试！');
        } else if (response.data == 3) {
          alert('这是你自己的书！');
        }
        return response.data;
      })
      .catch(error => {
        console.log(error);
      });
  },
  [GET_BOOK_COUNT](state) {
    axios.post('/list/getBookCount')
      .then(response => {
        state.bookCount = response.data;
      })
      .catch(error => {
        console.log(error);
      });
  },
  [GET_BOOK](state, pageNum) {
    axios.post('/list/getBook', {pageNum: pageNum})
      .then(response => {
        if (response.data != null) {
          state.bookInfo = [];
          for (let i = 0; i < response.data.length; i++) {
            state.bookInfo.push({
              id: response.data[i].id,
              name: response.data[i].name,
              howNew: response.data[i].newlevel,
              intro: response.data[i].description,
              price: response.data[i].price,
              bookImageUrl: response.data[i].imgurl
            })
          }
        } else {
          alert('获取失败，请重新获取！');
        }
      })
      .catch(error => {
        console.log(error);
      });
    console.log(state.bookInfo);
  },

  [GET_USER_BOOKS](state) {
    axios.post('/manager/getUserBooks', {userId: state.userInfo.id})
      .then(response => {
        if (response.data != null) {
          state.userBooks = [];
          for (let i = 0; i < response.data.length; i++) {
            state.userBooks.push({
              id: response.data[i].id,
              name: response.data[i].name,
              howNew: response.data[i].newlevel,
              intro: response.data[i].description,
              price: response.data[i].price,
              bookImageUrl: response.data[i].imageUrl
            })
          }
        }
        else {
          alert('请重新获取你的图书信息！');
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  [BOOK_ADD](state, book) {
    state.userBooks.push(book);

    axios.post('/manager/addBook',
      {
        userId: state.userInfo.id,
        bookId: book.id,
        intro: book.intro,
        name: book.name,
        publication: book.publication,
        howNew: book.howNew,
        bookImageUrl: book.bookImageUrl,
        price: book.price
      })
      .then(response => {
        if (response.data == true) {
          alert('添加成功！');
        }
        else {
          alert('请重新添加！');
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  [BOOK_UPDATE](state, book) {
    axios.post('/manager/updateBook',
      {
        userId: state.userInfo.id,
        id: book.id,
        intro: book.intro,
        name: book.name,
        publication: book.publication,
        howNew: book.howNew,
        bookImageUrl: book.bookImageUrl,
        price: book.price
      })
      .then(response => {
        if (response.data == true) {
          alert('修改成功');
        }
        else {
          for (let j = 0; j < state.userBooks.length; j++) {
            if (state.userBooks[j].id == book.oldBook.id) {
              state.userBooks[j] = book.oldBook;
            }
          }
          alert('修改失败');
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  [BOOK_DELETE](state, bookId) {
    axios.post('/manager/deleteBook',
      {
        bookId: bookId.bookId,
      })
      .then(response => {
        if (response.data == true) {
          for (let j = 0; j < state.userBooks.length; j++) {
            if (state.userBooks[j].id == bookId.bookId) {
              state.userBooks.splice(j, 1);
            }
          }
          alert('删除成功');
        }
        else {
          alert('删除失败');
        }
      })
      .catch(error => {
        console.log(error);
      });
  },

  [GET_CART](state) {
    axios.post('/cart/getCart', {userId: state.userInfo.id})
      .then(response => {
        if (response.data != null) {
          state.cart = [];
          for (let i = 0; i < response.data.length; i++) {
            state.cart.push({
              id: response.data[i].id,
              name: response.data[i].name,
              howNew: response.data[i].newlevel,
              intro: response.data[i].description,
              price: response.data[i].price,
              bookImageUrl: response.data[i].imageUrl
            })
          }
          console.log(state.cart);
        }
        else {
          alert('获取购物车失败，请重试！');
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  [CANCEL_BUY](state, bookId) {
    axios.post('/cart/cancelBook', {userId: state.userInfo.id, bookId: bookId.bookId})
      .then(response => {
        if (response.data != null) {
          state.cart = [];
          for (let i = 0; i < response.data.length; i++) {
            state.cart.push({
              id: response.data[i].id,
              name: response.data[i].name,
              howNew: response.data[i].newlevel,
              intro: response.data[i].description,
              price: response.data[i].price,
              bookImageUrl: response.data[i].imageUrl
            })
          }
        }
        else {
          alert('取消失败，请重试！');
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  [PAY](state) {
    axios.post('/cart/pay', {userId: state.userInfo.id})
      .then(response => {
        if (response.data == true) {
          state.cart = [];
        }
        else {
          alert('支付失败，请重试！');
        }
      })
      .catch(error => {
        console.log(error);
      });
  },

  [LOGIN](state, loginInfo) {
    setCookie('loginUser', state.userInfo.id, 10000);
    axios.post('/login',
      {
        userName: loginInfo.userName,
        password: loginInfo.password
      })
      .then(response => {
        if (response.data != null) {
          state.userInfo.id = response.data.userId;
          state.userInfo.userName = response.data.userName;
          state.userInfo.phoneNumber = response.data.phoneNumber;
          state.userInfo.headImageUrl = response.data.headImageUrl;
          state.userInfo.college = response.data.userCollege;
          setCookie('user', state.userInfo.id, 10000);
          alert('登录成功');
        } else {
          alert('密码或用户名错误，请重新输入!');
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  [REGISTER](state, registerInfo) {
    setCookie('user', state.userInfo.id, 10000);
    console.log(getCookie('user'));
    axios.post('/register',
      {
        userName: registerInfo.userName,
        password: registerInfo.password,
        headImageUrl: registerInfo.headImageUrl,
        college: registerInfo.college,
        phoneNumber: registerInfo.phoneNumber
      })
      .then(response => {
        if (response.data != null) {
          state.userInfo.id = response.data.userId;
          state.userInfo.userName = response.data.userName;
          state.userInfo.phoneNumber = response.data.phoneNumber;
          state.userInfo.headImageUrl = response.data.headImageUrl;
          state.userInfo.college = response.data.userCollege;
          setCookie('user', state.userInfo.id, 10000);
          alert('注册成功！');
        } else {
          alert('用户名已被使用，请更换用户名！');
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  [LOGOUT](state) {
    state.userInfo.id = -1;
    setCookie('user', state.userInfo.id, 10000);
  },
  [GET_USER_INFO](state){
    axios.post('/getUserInfo',
      {
        userId : getCookie('user')
      })
      .then(response => {
        state.userInfo.id = response.data.userId;
        state.userInfo.userName = response.data.userName;
        state.userInfo.phoneNumber = response.data.phoneNumber;
        state.userInfo.headImageUrl = response.data.headImageUrl;
        state.userInfo.college = response.data.userCollege;
      })
      .catch(error => {
        console.log(error);
      });
  }
}
export default mutations



