import Vue from 'vue'
import Vuex from 'vuex'
import * as getters from './getters'
import actions from './actions'
import mutations from './mutations'

Vue.use(Vuex)
const state = {
  bookInfo: [
  ],
  cart: [],
  userInfo: {
    id: 1,
    userName: '',
    headImageUrl: '',
    college: '',
    phoneNumber: -1
  },
  userBooks: [],
  bookCount: 23,
}


const store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions
});

export default store;



