<template>
  <div>
    <nav-bar :style="{position:'fixed',width:'100%',top:'0',height:'60px'}"></nav-bar>
    <router-view></router-view>
  </div>
</template>

<script>
  import NavBar from './components/NavBar'

  export default {
    name: 'app',
    components: {
      'nav-bar': NavBar,
    },
    created: function () {
      document.getElementsByTagName('body')[0].style.width = window.screen.width - 13 + 'px';
    }
  }
</script>
<style>
  .bookimg {
    height: 200px;
  }

  .text {
    text-align: center;
  }

  .listContainer {
    padding-top: 30px;
  }

  body {
    background: -webkit-linear-gradient(315deg, #2980b9 0%, #9b59b6 100%);
    background: linear-gradient(135deg, #2980b9 0%, #9b59b6 100%);
    /*filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#1e5799', endColorstr='#7db9e8', GradientType=1);*/
  }
</style>





