<template>
  <div>
    <div class="tab">
      <Table border :columns="columns7" :data="cart"></Table>
      <p class="money">总计：{{sum}}元</p>
    </div>
    <div align="right" style="margin-right: 200px">
      <Button type="success" @click="pay">支付</Button>
    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    name: 'Cart',
    created(){
      this.$store.dispatch('getCart');
    },
    data() {
      return {
        columns7: [
          {
            title: '书本id',
            key: 'id',
            align: 'center',
          },
          {
            title: '书名',
            key: 'name',
            align: 'center',
          },
          {
            title: '价格',
            key: 'price',
            align: 'center',
          },
          {
            title: '操作',
            key: 'action',
            width: 150,
            align: 'center',
            render: (h, params) => {
              return h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.$store.dispatch('cancelBook', {
                      bookId: this.cart[params.index].id
                    })

                  }
                }
              }, '删除')
            }
          }
        ]
      }
    },
    computed: {
      ...mapGetters({
        cart: 'cart'
      }),
      allbooks() {
        return this.cart;
      },
      sum() {
        let allMoney = 0;
        this.cart.map(item => {
          allMoney += item.price
        })
        return allMoney
      }
    },
    methods: {
      pay: function () {
        this.$store.dispatch('pay');
      }
    }
  }
</script>
<style>
  .tab {
    margin: 60px;
  }

  .money {
    margin: 40px;
  }
</style>





