<template>
  <Menu mode="horizontal" active-name="1">
    <router-link to="/home">
      <Menu-item name="1">
        <Icon type="ios-paper"></Icon>
        首页
      </Menu-item>
    </router-link>

    <router-link to="/list">
      <Menu-item name="2">
        <Icon type="ios-people"></Icon>
        书单
      </Menu-item>
    </router-link>

    <Submenu name="3" v-if="user.id!=-1">
      <template slot="title">
        <Icon type="ios-people"></Icon>
        管理图书
      </template>
      <MenuGroup title="操作">
        <router-link to="/manager/add">
          <MenuItem name="add">增加图书</MenuItem>
        </router-link>
        <router-link to="/manager/update">
          <MenuItem name="update">更新图书</MenuItem>
        </router-link>
        <router-link to="/manager/delete">
          <MenuItem name="delete">删除图书</MenuItem>
        </router-link>
      </MenuGroup>
    </Submenu>

    <Submenu name="101" v-else>
      <template slot="title">
        <Icon type="ios-people"></Icon>
        管理图书
      </template>
      <MenuGroup title="操作">
        <MenuItem name="add" @click.native="tips_login">增加图书</MenuItem>
        <MenuItem name="update" @click.native="tips_login">更新图书</MenuItem>
        <MenuItem name="delete" @click.native="tips_login">删除图书</MenuItem>
      </MenuGroup>
    </Submenu>

    <router-link to="/cart">
      <Menu-item name="4">
        <Badge :count="this.$store.state.cart.length">
        <Icon type="ios-cart-outline"></Icon>
        购物车
        </Badge>
      </Menu-item>
    </router-link>

    <router-link to="/login" v-if="user.id==-1">
      <Menu-item name="5" style="{right: 0}">
        <Icon type="ios-contact"></Icon>
        登陆/注册
      </Menu-item>
    </router-link>

    <Submenu name="6" v-else>
      <template slot="title">
        <Icon type="ios-contact"></Icon>
        {{user.userName}}
      </template>
      <MenuItem name="quit" @click.native="tips_logout">退出登录</MenuItem>
    </Submenu>
  </Menu>
</template>
<script>
  import {mapGetters} from 'vuex'

  const getCookie = function (c_name) {
    if (document.cookie.length > 0) {
      var c_start = document.cookie.indexOf(c_name + "=");
      if (c_start != -1) {
        c_start = c_start + c_name.length + 1;
        var c_end = document.cookie.indexOf(";", c_start);
        if (c_end == -1) c_end = document.cookie.length;
        return unescape(document.cookie.substring(c_start, c_end));
      }
    }
    return "";
  };
  export default {
    name: 'nav-bar',
    computed: {
      ...mapGetters({
        user: 'user',
      }),

    },
    data() {
      return {
        loginName: '登录'
      }
    },
    created() {
      this.$store.state.userInfo.id = getCookie('user');
      if(this.$store.state.userInfo.id != -1) {
        this.$store.dispatch('getUserInfo');
      }
      console.log(getCookie('user'));
    },
    methods: {
      tips_login() {
        alert('登陆后可用！');
        this.$router.push('/login');
      },
      tips_logout() {
        alert('成功退出登录！');
        this.$store.dispatch('logout');
        this.$router.push('/home');
      },
    }
  }
</script>



