<template>
  <div class="container">
    <Col span="5" align="center" class="text">
      <!--<div v-bind:class="{'element-card' : true,'open' : rotaIndex == index}" v-on:click="clickCard(index)">-->
      <div v-bind:class="{'element-card' : true ,'open':this.toadd===true}">
        <div class="front-facing">
          <!--<router-link :to="{ name:'detail',params:{id:book.id}}">详情</router-link>-->
          <br><br><br>
          如果您有想要出售的二手书，可点击下方按钮
          <br><br><br>
          <Button type="primary" long @click="toAddPage">增加书本</Button>
        </div>
        <div v-bind:class="{'back-facing':true}">
          <Form :label-width="80">
            <FormItem label="书名" style="margin: 0">
              <!--单向绑定-->
              <label>
                <Input type="text" v-model="newbook.name" placeholder="请输入书名"/>
              </label>
            </FormItem>
            <FormItem label="出版时间" style="margin: 0">
              <Input type="text" v-model="newbook.publication" placeholder="请输入书的出版时间"/>
            </FormItem>
            <FormItem label="新旧程度" style="margin: 0">
              <Input type="text" v-model="newbook.howNew" placeholder="请输入书的新旧程度"/>
            </FormItem>
            <FormItem label="价格" style="margin: 0">
              <Input type="text" v-model="newbook.price" placeholder="请输入您定的价格"/>
            </FormItem>
            <FormItem label="描述" style="margin: 0">
              <Input type="text" v-model="newbook.intro" placeholder="请描述您的书籍"/>
            </FormItem>
          </Form>
          <br>
          <Button type="success" long @click="add(newbook)" style="top:20px">确认提交</Button>
          <br>
          <Button type="error" long @click="toAddPage" style="top:20px">返回</Button>
        </div>
      </div>
    </Col>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex';

  export default {

    name: 'Add',
    computed: {
      ...mapGetters({
        books: 'books'
      })
    },
    data() {
      return {
        isActive: true,
        toadd: false,
        rotaIndex: -1,
        formInline: {
          userName: '',
          password: ''
        },
        newbook: {
          intro: "",
          name: "",
          publication: "",
          howNew: 9,
          bookImageUrl: "",
          price: 15
        },
      }
    },

    methods: {
      add: function (newbook) {
        this.toAddPage();
        if (this.newbook.name != "") {
          this.$store.dispatch('addBook', {
            intro: newbook.intro,
            name: newbook.name,
            publication: newbook.publication,
            howNew: newbook.howNew,
            bookImageUrl: newbook.bookImageUrl,
            price: newbook.price
          })
        }
        else {
          alert("请输入书名！")
        }
      },
      toAddPage: function () {
        this.toadd = !this.toadd;
      },

    },
  }
</script>




