<template>
  <div class="container" id="update">
    <Col span="5" align="center" v-for="(book,index) in books">
      <div v-bind:class="{'element-card' : true}">
        <div class="front-facing">
          <p slot="title">{{book.name}}</p>
          <img class="bookimg" :src="book.bookImageUrl" alt="">
          <p>售价：{{book.price}}</p>
          <Button type="error" long @click="todelete(index)">删除</Button>
          <!--<router-link :to="{ name:'detail',params:{id:book.id}}">详情</router-link>-->
        </div>
      </div>
    </Col>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex';

  export default {

    name: 'delete',
    computed: {
      ...mapGetters({
        books: 'userBooks'  //TODO 需要修改
      })
    },
    created: function () {
      this.$store.dispatch('getUserBooks');
    },
    data() {
      return {}
    },
    methods: {
      todelete(index) {
        this.$store.dispatch('deleteBook', {
          bookId: this.books[index].id
        })
      }
    }
  }
</script>
<style>
</style>




